import axios from "axios";
export default {
  data: function data() {
    return {
      story: {}
    };
  },
  mounted: function mounted() {
    this.get_news();
  },
  methods: {
    back: function back() {
      this.$router.go(-1);
    },
    get_news: function get_news() {
      var _this = this;

      return axios.get("/api/4/news/".concat(this.$route.params.id)).then(function (res) {
        if (res.status === 200) {
          _this.story = res.data;
        }
      });
    }
  }
};